<template>
  <d2-container>
    <template slot="header">
      <instence-select :isupf="false" @changeInstence="changeInstance"></instence-select>
    </template>

    <el-card class="box-card">
      <el-form label-position="left" class="demo-table-expand" size="mini">
        <el-form-item label="平均QoS流数："
          ><span>{{ serverData.upf_mean_qos_flows }}</span></el-form-item
        >
        <el-form-item label="最大QoS流数："
          ><span>{{ serverData.upf_max_qos_flows }}</span></el-form-item
        >
      </el-form>
    </el-card>

    <el-row>
      <el-select
        v-model="nsName"
        placeholder="请选择ns-name"
        size="small"
        filterable
        clearable
      >
        <el-option
          v-for="item in nsNameOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>
      <el-select
        v-model="dnnName"
        placeholder="请选择dnn-name"
        size="small"
        filterable
        clearable
      >
        <el-option
          v-for="item in dnnNameOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>
      <el-button size="mini" type="primary" @click="changeNsName"
        >筛选</el-button
      >
    </el-row>
    <br />

    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane
        v-for="(item, index) in tabPaneList"
        :key="index"
        :label="item.tabLabel"
        :name="item.tabLabel"
      >
        <el-table :data="serverData[item.dataKey]" style="width: 100%">
          <el-table-column prop="id" label="ID" width="180"> </el-table-column>
          <el-table-column
            v-if="index === 0"
            prop="ns_name"
            label="ns_name"
            width="180"
          >
          </el-table-column>
          <el-table-column v-else prop="dnn_name" label="dnn_name" width="180">
          </el-table-column>
          <el-table-column :prop="item.columKey" label="平均QOS流">
          </el-table-column>
          <el-table-column :prop="item.colummaxKey" label="最大QOS流">
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </d2-container>
</template>

<script>
import {
  fetchGetQosFlow,
} from "@/api/demo/index";
import instenceSelect from '../compoents/instenceSelect.vue';
export default {
  components: { instenceSelect },
  name: "qosFlow",
  data() {
    return {
      activeName: "分NS统计",
      instanceValue: "", //当前选中的实例
      isFirst: true,
      loading: false,
      nsName: "",
      nsNameOptions: [],
      dnnName: "",
      dnnNameOptions: [],
      serverData: {},
      oldServerData: {},
      tabPaneList: [
        {
          tabLabel: "分NS统计",
          dataKey: "meanQosFlowsNsList",
          columKey: "mean_qos_flows",
          colummaxKey: "max_qos_flows",
        },
        {
          tabLabel: "分DNN统计",
          dataKey: "meanQosFlowsDnnList",
          columKey: "mean_qos_flows",
          colummaxKey: "max_qos_flows",
        }
      ],
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.serverData = [];
      this.instanceValue = val

      // 获取告警数据
      this.getData();
    },
    // 筛选
    changeNsName() {
      if (this.nsName) {
        this.serverData.meanQosFlowsNsList.filter(
          (item) => item.ns_name === this.nsName
        );
        this.serverData.maxQosFlowsNsList.filter(
          (item) => item.ns_name === this.nsName
        );
      } else {
        this.serverData.meanQosFlowsNsList = this.oldServerData.meanQosFlowsNsList;
        this.serverData.maxQosFlowsNsList = this.oldServerData.maxQosFlowsNsList;
      }
      if (this.dnnName) {
        this.serverData.meanQosFlowsDnnList.filter(
          (item) => item.dnn_name === this.dnnName
        );
        this.serverData.maxQosFlowsDnnList.filter(
          (item) => item.dnn_name === this.dnnName
        );
      } else {
        this.serverData.meanQosFlowsDnnList = this.oldServerData.meanQosFlowsDnnList;
        this.serverData.maxQosFlowsDnnList = this.oldServerData.maxQosFlowsDnnList;
      }
    },
    // 获取数据
    getData() {
      this.loading = true;
      fetchGetQosFlow({
        request_type: "qos perform",
        instance_id: this.instanceValue,
        sequence_no: 4001,
      })
        .then((response) => {
          this.loading = false;
          console.log(response);
          if ((response != null) & (response != [])) {
            this.oldServerData = response;
            this.serverData = response;
            // 获取选择框数组
            if (this.serverData.meanQosFlowsNsList) {
              let arr1 = this.serverData.meanQosFlowsNsList.map(
                (item) => item.ns_name
              );
              let arr2 = this.serverData.maxQosFlowsNsList.map(
                (item) => item.ns_name
              );
              let arr3 = this.serverData.meanQosFlowsDnnList.map(
                (item) => item.dnn_name
              );
              let arr4 = this.serverData.maxQosFlowsDnnList.map(
                (item) => item.dnn_name
              );
              this.nsNameOptions = [...new Set([...arr1, ...arr2])];
              this.dnnNameOptions = [...new Set([...arr3, ...arr4])];
            
              // 拼接组装表格数据
              this.serverData.meanQosFlowsNsList.forEach((element, index) => {
                element.max_qos_flows = this.serverData.maxQosFlowsNsList[index].max_qos_flows
              })
              this.serverData.meanQosFlowsDnnList.forEach((element, index) => {
                element.max_qos_flows = this.serverData.maxQosFlowsDnnList[index].max_qos_flows
              })
            }
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
  },
};
</script>

